<template>
	<div class="modifed-root">
		<div class="row frm-row">
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('common.metaHistory.before') }}
						</span>
					</label>
					<div>
						<div class="detail-content-common diff-form">
							<component :is="beforeContents"></component>
						</div>
					</div>
				</div>
			</div>
			<div class="col-lg-6">
				<div class="form-group">
					<label class="control-label">
						<span>
							{{ this.$t('common.metaHistory.after') }}
						</span>
					</label>
					<div>
						<div class="detail-content-common diff-form">
							<component :is="afterContents"></component>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div v-show="isViewSpinner">
			<div class="modifed-spinner-bg"></div>
			<div class="spinner"></div>
		</div>
	</div>
</template>
<script>
import DetailBasic from '@/components/detail/DetailBasic.vue';
import { escapeHtml, getHttpUrl, getContextPath } from '@/utils/common.js';

export default {
	name: 'Modifed',
	extends: DetailBasic,
	data() {
		return {
			isViewSpinner: false,
			beforeContents: {
				template: `<span></span>`
			},
			afterContents: {
				template: `<span></span>`
			},
			worker: null,
			WORKER_RESOURCE: `
            self.onmessage = e => {
               importScripts(e.data.url);

               const diff = Diff.diffWordsWithSpace(e.data.beforeDataString, e.data.afterDataString, {
                  ignoreWhitespace: false,
                  newlineIsToken: false,
               });

               postMessage({ diff: diff });
            }
         `
		};
	},
	methods: {
		initData() {
			if (!this.detailData.beforeDataString && !this.detailData.afterDataString) return;

			if (!this.detailData.beforeDataString) this.detailData.beforeDataString = ``;
			if (!this.detailData.afterDataString) this.detailData.afterDataString = ``;

			if (this.worker) this.worker.terminate();
			this.worker = new Worker(URL.createObjectURL(new Blob([this.WORKER_RESOURCE], { type: `application/javascript` })));

			this.isViewSpinner = true;

			this.worker.postMessage({
				url:
					'production' === process.env.NODE_ENV
						? `${location.origin}${getContextPath()}/custom/js/diff.js`
						: getHttpUrl(`/custom/js/diff.js`),
				beforeDataString: this.detailData.beforeDataString,
				afterDataString: this.detailData.afterDataString
			});

			this.worker.onmessage = e => {
				const diff = e.data.diff;

				const beforeArr = [];
				const afterArr = [];

				diff.forEach(diff => {
					const content = escapeHtml(diff.value).replace(/\s/g, `&nbsp;`);

					if (diff.added) afterArr.push(`<ins class="diff-ins">${content}</ins>`);
					else if (diff.removed) beforeArr.push(`<del class="diff-del">${content}</del>`);
					else {
						beforeArr.push(content);
						afterArr.push(content);
					}
				});

				this.beforeContents = {
					template: `<span>${beforeArr.join('')}</span>`
				};

				this.afterContents = {
					template: `<span>${afterArr.join('')}</span>`
				};

				this.isViewSpinner = false;
			};
		}
	}
};
</script>

<style lang="scss">
.modifed-root {
	position: relative;
	height: 100%;

	.row {
		height: 100%;

		.col-lg-6 {
			height: 100%;

			.form-group {
				height: 100%;
			}

			.form-group > div {
				height: calc(100% - 24px);
			}
		}
	}
}

.modifed-spinner-bg {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	opacity: 0.6;
}

.diff-form {
	background-color: #f5f6fb;
	width: 100%;
	height: 100%;
	padding: 0.375rem 0.5rem;
	border: 1px solid #babbc2;
	border-radius: 0.25rem;
	overflow-y: scroll;
}
.diff-ins {
	background: #ddffdd;
	color: #406619;
	text-decoration: none;
}
.diff-del {
	background: #fee8e9;
	color: #b30000;
	text-decoration: none;
}
</style>
